.footer {
  padding: 50px 0;
  background-image: url("../img/footer-bg.png");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

.footer-title {
  font-size: 90.1px;
  line-height: 120px;
  font-weight: @bold;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #000000;
  text-align: right;
}

.footer-blocks {
  display: flex;
  justify-content: space-between;
  margin-top: 250px;
  padding: 50px;
  background-color: #000000;
}

.custom {
  display: flex;
  align-items: flex-start;
  padding-left: 30px;
}

.footer-blocks-content {
  display: flex;
}

.footer-blocks-content:nth-child(1) {
  width: 60%;
  justify-content: space-between;
}

.footer-block {
  margin: 5px 10px;
}

.footer-block-img {
  width: 33px;
  transform: rotate(45deg);
}

.footer-block-menu-link {
  font-size: 12px;
  line-height: 40px;
  font-weight: @thin;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: #ffffff;
}

.footer-block-menu-link:hover {
  opacity: 0.5;
}

.web-designer,
.web-designer-contact {
  font-size: 12px;
  line-height: 40px;
  font-weight: @thin;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: #ffffff;
  opacity: 0.5;
}

.web-designer-contact:hover {
  opacity: 1;
}

.craft-logo {
  width: 118px;
}

@media @small-widths {
  .footer-title {
    text-align: center;
    font-size: 56px;
  }
  .footer-blocks {
    flex-direction: column;
    align-items: center;
  }
  .footer-blocks-content:nth-child(1) {
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .footer-block-img {
    display: none;
  }
  .custom {
    padding-left: 0;
  }
}

@media @exxSmall-widths {
  .footer-title {
    font-size: 40px;
  }
  .footer-blocks-content {
    text-align: center;
    justify-content: center;
  }
  .craft-logo {
    margin: 0 auto;
  }
}
