.header {
  padding-top: 47px;
  background-image: url("../img/main-bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header-top {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-logo-img {
  max-width: 33px;
}

.header-menu-wrapper {
  display: flex;
  align-items: center;
}

.header-menu {
  display: flex;
  align-items: center;
}

.menu {
  display: flex;
}

.menu-item {
  margin: 0 14px;
  cursor: pointer;
}

.menu-item-link {
  color: @gray;
  font-size: 12px;
  line-height: 1.2;
  font-weight: @thin;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-decoration: none;
}

.menu-item:hover .menu-item-link,
.menu-item-link:active {
  color: @dark;
  font-weight: @medium;
}

.dropdown-btn {
  display: none;
  cursor: pointer;
}

.bar1,
.bar2,
.bar3 {
  display: block;
  width: 35px;
  height: 3px;
  background-color: #333;
  margin: 6px 0;
  transition: 0.4s;
}

.subheader-content {
  padding: 223px 0 382px;
}

.header-bottom {
  padding: 90px 0;
}

.header-bottom-content {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 27px 0;
}

.header-bottom-content-block {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  margin: 10px 36px;
  width: 25%;
}

.header-bottom-content-block-img {
  max-width: 60px;
}

.header-bottom-content-block-title {
  margin: 35px 0 32px;
  font-size: 14px;
  line-height: 22px;
  font-weight: @medium;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: @dark;
}

@media @small-widths {
  .header-menu {
    position: absolute;
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
    display: none;
    width: 100%;
    text-align: center;
  }
  .menu {
    flex-direction: column;
    background-color: #fff;
  }
  .menu-item {
    margin: 0;
    padding: 5px 0;
  }
  .menu-item:hover {
    background-color: rgb(228, 228, 228);
  }
  .header-top {
    height: 50px;
  }

  .header-bottom-content {
    flex-wrap: wrap;
  }
  .header-bottom-content-block {
    width: 100%;
  }
  .dropdown-btn {
    display: block;
  }
  .subheader-content {
    text-align: center;
    padding-bottom: 200px;
  }
}

