.join-title-wrapper {
  padding: 92px 0;
  background-color: #fff;
  text-align: center;
}

.join-blocks {
  display: flex;
  justify-content: space-between;
  margin: 78px 0;
}

.join-block {
  padding: 40px 10px;
  width: 30%;
  text-align: center;
}

.join-block:nth-child(2) {
  background-color: #000000;
}

.join-block-title {
  font-size: 12px;
  font-weight: @thin;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.join-block:nth-child(2) .join-block-title {
  color: #ffffff;
}

.join-block-pice {
  font-size: 50px;
  line-height: 120px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #000000;
}

.join-block:nth-child(2) .join-block-pice {
  color: #ffffff;
}

.join-block-descr {
  padding-bottom: 30px;
  font-size: 14px;
  line-height: 22px;
  font-weight: @thin;
  color: #000000;
  opacity: 0.5;
}

.join-block:nth-child(2) .join-block-descr {
  color: #ffffff;
}

@media @small-widths {
  .join-blocks {
    flex-direction: column;
  }
  .join-block {
    width: 100%;
  }
}
