.iphone {
  position: relative;
}

.iphone {
  height: 650px;
  background-color: #26e6e6;
  transition: background-color 0.3s;
}
.iphone.change-bg {
  background-color: #1db6b6;
}
.iphone-wrapper {
  position: relative;
}

.iphone-img-block {
  position: absolute;
  top: 50px;
  left: 40%;
  transform: translate(-50%, 0);
}

.iphone-img {
  max-width: 650px;
}

.iphone-inside {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50px, -50px);
  max-width: 100px;
}

.iphone-inside-first {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.3s, visibility 0.3s;
}

.change-bg .iphone-inside-first {
  visibility: hidden;
  opacity: 0;
}

.iphone-inside-second {
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s;
}

.change-bg .iphone-inside-second {
  visibility: visible;
  opacity: 1;
}

.iphone-info {
  position: absolute;
  top: 50px;
  right: 20%;
  margin-left: auto;
  width: 386px;
  background-color: #fff;
  z-index: 2;
}

.iphone-info-blocks {
  padding: 35px 35px 0;
}

.iphone-info-block {
  display: flex;
  margin-bottom: 34px;
}
.iphone-info-block-img {
  margin-right: 24px;
  width: 54px;
  height: 54px;
}

@media @small-widths {
  .iphone-info {
    top: 100px;
    right: 0;
    width: 100%;
  }
  .iphone-img-block {
    display: none;
  }
  .iphone-info .btn-wrapper {
    padding: 30px 0;
  }
}
