.qotes-blocks {
  cursor: pointer;
}

.qotes-block {
  position: relative;
  margin: 20px;
  padding: 70px;
  text-align: center;
  background-color: #fff;
}

.qote-symbol {
  position: absolute;
  max-width: 24px;
  opacity: 0.2;
}

.top {
  top: 35px;
  left: 35px;
}

.bottom {
  bottom: 35px;
  right: 35px;
}

.qotes-block-descr {
  font-size: 42px;
  line-height: 1.2;
  font-weight: @thin;
}

.qotes-block-author {
  font-size: 12px;
  line-height: 1.2;
  font-weight: @medium;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #000000;
}

@media @small-widths {
  .qotes-block-descr {
    font-size: 26px;
  }
}

@media @exxSmall-widths {
  .qotes-block {
    padding: 40px;
  }
  .qotes-block-descr {
    font-size: 14px;
  }
}
