.doit {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px 0;
  align-items: center;
  overflow: hidden;
}

.doit-blocks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.doit-block {
  display: inline;
}

.doit-figure {
  position: relative;
}

.doit-figure:hover .doit-img {
  filter: brightness(0.5);
}

.doit-figure:hover .doit-btn-wrapper {
  display: block;
}

.doit-img {
  padding: 30px;
  max-width: 745px;
}

.doit-btn-wrapper {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}

.doit-btn-small {
  padding: 17px 25px;
}

@media @exxSmall-widths {
  .doit-img {
    padding: 5px;
  }
  .doit-btn-small {
    padding: 17px 15px;
  }
}
