.build-top-wrapper {
  background-color: #fff;
}
.build-top {
  margin: 0 auto;
  padding: 120px 0 108px;
  text-align: center;
  max-width: 592px;
}
.build-top-subtitle {
  font-size: 18px;
  line-height: 26px;
  font-weight: @medium;
  color: @dark;
}
.build-info {
  padding: 110px 0 102px;
}
.build-info-blocks {
  display: flex;
  justify-content: space-between;
}
.build-info-block {
  display: flex;
  flex-direction: column;
  width: 25%;
}

@media @small-widths {
  .build-info {
    padding: 65px 0 40px;
  }
  .build-info-blocks {
    flex-direction: column;
  }
  .build-info-block {
    width: 100%;
    margin: 20px 0;
  }
}
