.container {
  width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}
@media screen and (max-width: 1199px) {
  .container {
    width: 970px;
  }
}
@media screen and (max-width: 991px) {
  .container {
    width: 750px;
  }
}
@media screen and (max-width: 767px) {
  .container {
    width: 100%;
    padding: 0 10px;
  }
}
body {
  font-family: "Montserrat", sans-serif;
  background-color: #f5f6f8;
  min-width: 320px;
  min-height: 100vh;
  overflow-wrap: break-word;
  word-wrap: break-word;
  box-sizing: border-box;
}
img {
  display: block;
  width: 100%;
  height: auto;
}
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
.new {
  padding: 5px 10px;
  color: #fff;
  background-color: #26e6e6;
  font-size: 10px;
  line-height: 1.2;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-radius: 12px;
}
@media screen and (max-width: 991px) {
  .opened {
    display: block !important;
  }
}
.btn {
  font-size: 12px;
  line-height: 1.2;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-decoration: none;
}
.btn-wrapper {
  padding: 12px 0;
  text-align: center;
}
.blue-btn {
  padding: 17px 45px;
  color: #000000;
  background-color: #26e6e6;
  font-size: 12px;
  font-weight: 600;
  border-radius: 25px;
  text-transform: uppercase;
}
.blue-btn:hover {
  background-color: #1dbdbd;
  color: #fff;
}
.black-btn {
  padding: 13px 30px;
  color: #fff;
  background-color: #000000;
  border-radius: 2px;
}
.black-btn:hover {
  background-color: #000000ba;
}
.more {
  margin-top: auto;
  color: #232323;
  font-size: 12px;
  line-height: 1.2;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
}
.more:hover {
  color: #000000ba;
}
.btn-long {
  padding: 13px 147px;
}
.iphone-btns {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.iphone-btn {
  font-size: 60px;
  padding: 0px 25px;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 100%;
}
.iphone-next,
.iphone-prev {
  position: absolute;
  color: #fff;
  background-color: transparent;
  text-align: center;
  width: 70px;
  font-size: 60px;
  transform: rotate(-43deg);
  border: 1px solid #fff;
  border-radius: 50%;
  cursor: pointer;
}
.iphone-prev {
  top: 70px;
  right: 145px;
}
.iphone-next {
  right: -190px;
  bottom: 100px;
}
.ipad-prev {
  position: absolute;
  left: -45px;
  top: 50%;
  transform: translateY(-50%);
  padding: 18px 22px;
  color: #fff;
  border: 3px solid #fff;
  opacity: 0.2;
  border-radius: 50%;
}
.ipad-prev:hover {
  opacity: 0.6;
}
@media screen and (max-width: 991px) {
  .btn-long {
    padding: 13px 100px;
  }
}
@media screen and (max-width: 479px) {
  .blue-btn {
    padding: 17px 17px;
  }
}
.text {
  font-size: 14px;
  line-height: 22px;
  font-weight: 300;
  color: #8c8d8e;
}
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;
}
.block-title {
  font-size: 72px;
  line-height: 1.2;
  font-weight: 300;
  color: #000000;
}
@media screen and (max-width: 991px) {
  .block-title {
    font-size: 50px;
  }
}
.header {
  padding-top: 47px;
  background-image: url("../img/main-bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header-top {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-logo-img {
  max-width: 33px;
}
.header-menu-wrapper {
  display: flex;
  align-items: center;
}
.header-menu {
  display: flex;
  align-items: center;
}
.menu {
  display: flex;
}
.menu-item {
  margin: 0 14px;
  cursor: pointer;
}
.menu-item-link {
  color: #8c8d8e;
  font-size: 12px;
  line-height: 1.2;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-decoration: none;
}
.menu-item:hover .menu-item-link,
.menu-item-link:active {
  color: #232323;
  font-weight: 600;
}
.dropdown-btn {
  display: none;
  cursor: pointer;
}
.bar1,
.bar2,
.bar3 {
  display: block;
  width: 35px;
  height: 3px;
  background-color: #333;
  margin: 6px 0;
  transition: 0.4s;
}
.subheader-content {
  padding: 223px 0 382px;
}
.header-bottom {
  padding: 90px 0;
}
.header-bottom-content {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 27px 0;
}
.header-bottom-content-block {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  margin: 10px 36px;
  width: 25%;
}
.header-bottom-content-block-img {
  max-width: 60px;
}
.header-bottom-content-block-title {
  margin: 35px 0 32px;
  font-size: 14px;
  line-height: 22px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #232323;
}
@media screen and (max-width: 991px) {
  .header-menu {
    position: absolute;
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
    display: none;
    width: 100%;
    text-align: center;
  }
  .menu {
    flex-direction: column;
    background-color: #fff;
  }
  .menu-item {
    margin: 0;
    padding: 5px 0;
  }
  .menu-item:hover {
    background-color: #e4e4e4;
  }
  .header-top {
    height: 50px;
  }
  .header-bottom-content {
    flex-wrap: wrap;
  }
  .header-bottom-content-block {
    width: 100%;
  }
  .dropdown-btn {
    display: block;
  }
  .subheader-content {
    text-align: center;
    padding-bottom: 200px;
  }
}
.footer {
  padding: 50px 0;
  background-image: url("../img/footer-bg.png");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}
.footer-title {
  font-size: 90.1px;
  line-height: 120px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #000000;
  text-align: right;
}
.footer-blocks {
  display: flex;
  justify-content: space-between;
  margin-top: 250px;
  padding: 50px;
  background-color: #000000;
}
.custom {
  display: flex;
  align-items: flex-start;
  padding-left: 30px;
}
.footer-blocks-content {
  display: flex;
}
.footer-blocks-content:nth-child(1) {
  width: 60%;
  justify-content: space-between;
}
.footer-block {
  margin: 5px 10px;
}
.footer-block-img {
  width: 33px;
  transform: rotate(45deg);
}
.footer-block-menu-link {
  font-size: 12px;
  line-height: 40px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: #ffffff;
}
.footer-block-menu-link:hover {
  opacity: 0.5;
}
.web-designer,
.web-designer-contact {
  font-size: 12px;
  line-height: 40px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: #ffffff;
  opacity: 0.5;
}
.web-designer-contact:hover {
  opacity: 1;
}
.craft-logo {
  width: 118px;
}
@media screen and (max-width: 991px) {
  .footer-title {
    text-align: center;
    font-size: 56px;
  }
  .footer-blocks {
    flex-direction: column;
    align-items: center;
  }
  .footer-blocks-content:nth-child(1) {
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .footer-block-img {
    display: none;
  }
  .custom {
    padding-left: 0;
  }
}
@media screen and (max-width: 479px) {
  .footer-title {
    font-size: 40px;
  }
  .footer-blocks-content {
    text-align: center;
    justify-content: center;
  }
  .craft-logo {
    margin: 0 auto;
  }
}
.iphone {
  position: relative;
}
.iphone {
  height: 650px;
  background-color: #26e6e6;
  transition: background-color 0.3s;
}
.iphone.change-bg {
  background-color: #1db6b6;
}
.iphone-wrapper {
  position: relative;
}
.iphone-img-block {
  position: absolute;
  top: 50px;
  left: 40%;
  transform: translate(-50%, 0);
}
.iphone-img {
  max-width: 650px;
}
.iphone-inside {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50px, -50px);
  max-width: 100px;
}
.iphone-inside-first {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.3s, visibility 0.3s;
}
.change-bg .iphone-inside-first {
  visibility: hidden;
  opacity: 0;
}
.iphone-inside-second {
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s;
}
.change-bg .iphone-inside-second {
  visibility: visible;
  opacity: 1;
}
.iphone-info {
  position: absolute;
  top: 50px;
  right: 20%;
  margin-left: auto;
  width: 386px;
  background-color: #fff;
  z-index: 2;
}
.iphone-info-blocks {
  padding: 35px 35px 0;
}
.iphone-info-block {
  display: flex;
  margin-bottom: 34px;
}
.iphone-info-block-img {
  margin-right: 24px;
  width: 54px;
  height: 54px;
}
@media screen and (max-width: 991px) {
  .iphone-info {
    top: 100px;
    right: 0;
    width: 100%;
  }
  .iphone-img-block {
    display: none;
  }
  .iphone-info .btn-wrapper {
    padding: 30px 0;
  }
}
.build-top-wrapper {
