.ipad {
  position: relative;
}
.ipad-wrapper {
  padding: 128px;
  background-color: #000000;
  color: #ffffff;
  border-radius: 2px;
}

.ipad-content {
  width: 65%;
}

.ipad-title {
  color: #ffffff;
}

.ipad-btn-wrapper {
  text-align: left;
}

.ipad-descr {
  margin: 49px 0 240px;
  max-width: 400px;
}

.ipad-device {
  position: absolute;
  bottom: -100px;
  right: 0;
  background-image: url("../img/iPad.png");
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
}

.ipad-device-wrapper {
  padding: 148px 0 422px 625px;
}

.ipad-device-img {
  position: absolute;
  top: 31px;
  left: 720px;
  height: 80%;
  cursor: pointer;
}

@media @medium-widths {
  .ipad-device {
    bottom: -167px;
  }
}

@media @small-widths {
  .ipad-wrapper {
    padding: 30px;
  }
  .ipad-content {
    width: 100%;
  }
  .ipad-title {
    text-align: center;
    font-size: 40px;
  }
  .ipad-descr {
    margin: 30px 0;
    max-width: 100%;
    text-align: center;
  }
  .ipad-btn-wrapper {
    text-align: center;
  }
  .ipad-device {
    display: none;
  }
}
