.features {
  margin-top: 120px;
}

.features-title {
  margin-bottom: 30px;
  font-size: 12px;
  line-height: 1.2;
  font-weight: @medium;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #000000;
}

.features-subtitle {
  margin: auto;
  font-size: 18px;
  text-align: center;
  line-height: 26px;
  font-weight: @medium;
  color: @dark;
  width: 75%;
}

.features-blocks {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 80px 0;
}

.features-block {
  display: flex;
  align-items: flex-start;
  padding: 20px;
  width: 30%;
}

.features-block-img {
  margin-right: 20px;
  max-width: 60px;
}

.features-block-title {
  font-size: 18px;
  line-height: 26px;
  font-weight: @medium;
}

.features-block-descr {
  font-size: 14px;
  line-height: 22px;
  font-weight: @medium;
  opacity: 0.5;
}

@media @small-widths {
  .features-subtitle {
    width: 100%;
  }
  .features-block {
    width: 100%;
  }
}
