.btn {
  font-size: 12px;
  line-height: 1.2;
  font-weight: @medium;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-decoration: none;
}

.btn-wrapper {
  padding: 12px 0;
  text-align: center;
}

.blue-btn {
  padding: 17px 45px;
  color: #000000;
  background-color: #26e6e6;
  font-size: 12px;
  font-weight: @medium;
  border-radius: 25px;
  text-transform: uppercase;
}

.blue-btn:hover {
  background-color: #1dbdbd;
  color: #fff;
}

.black-btn {
  padding: 13px 30px;
  color: #fff;
  background-color: #000000;
  border-radius: 2px;
}

.black-btn:hover {
  background-color: #000000ba;
}

.more {
  margin-top: auto;
  color: @dark;
  font-size: 12px;
  line-height: 1.2;
  font-weight: @medium;
  text-transform: uppercase;
  text-decoration: none;
}
.more:hover {
  color: #000000ba;
}

.btn-long {
  padding: 13px 147px;
}

.iphone-btns {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.iphone-btn {
  font-size: 60px;
  padding: 0px 25px;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 100%;
}

.iphone-next,
.iphone-prev {
  position: absolute;
  color: #fff;
  background-color: transparent;
  text-align: center;
  width: 70px;
  font-size: 60px;
  transform: rotate(-43deg);
  border: 1px solid #fff;
  border-radius: 50%;
  cursor: pointer;
}

.iphone-prev {
  top: 70px;
  right: 145px;
}

.iphone-next {
  right: -190px;
  bottom: 100px;
}

.ipad-prev {
  position: absolute;
  left: -45px;
  top: 50%;
  transform: translateY(-50%);
  padding: 18px 22px;
  color: #fff;
  border: 3px solid #fff;
  opacity: 0.2;
  border-radius: 50%;
}
.ipad-prev:hover {
  opacity: 0.6;
}

@media @small-widths {
  .btn-long {
    padding: 13px 100px;
  }
}

@media @exxSmall-widths {
  .blue-btn {
    padding: 17px 17px;
  }
}
